"use client";

import React, { useState, useEffect } from "react";
import Image from "next/image";
import Link from "next/link";
import { Search, Filter, Loader2 } from "lucide-react";
import { motion } from "framer-motion";
import { Input } from "@/components/ui/Input";
import { Button } from "@/components/ui/Button";
import { Card } from "@/components/ui/Card";
import { formatCurrency } from "@/lib/utils";
import { useCart } from "@/components/cart/CartProvider";
import type { Product } from "@/lib/product-service";

export default function ProductsPage() {
    const [searchQuery, setSearchQuery] = useState("");
    const { addItem } = useCart();
    const [selectedCategory, setSelectedCategory] = useState("All");
    const [products, setProducts] = useState<Product[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        fetch("/api/products")
            .then(res => res.json())
            .then(data => setProducts(data))
            .catch(err => console.error(err))
            .finally(() => setLoading(false));
    }, []);

    const categories = ["All", ...Array.from(new Set(products.map(p => p.category)))];

    const filteredProducts = products.filter(product => {
        const matchesSearch = product.name.toLowerCase().includes(searchQuery.toLowerCase());
        const matchesCategory = selectedCategory === "All" || product.category === selectedCategory;
        return matchesSearch && matchesCategory;
    });

    if (loading) return (
        <div className="flex items-center justify-center min-h-[50vh]">
            <Loader2 className="w-8 h-8 animate-spin text-primary" />
        </div>
    );

    return (
        <div className="container-custom py-24 md:py-32">
            <div className="flex flex-col mb-20 space-y-12">
                <div className="max-w-2xl">
                    <h1 className="text-5xl md:text-7xl font-bold tracking-tighter uppercase leading-[0.9] mb-6">
                        The Collection
                    </h1>
                    <p className="text-neutral-500 font-medium text-lg leading-relaxed">
                        Curated minimalist streetwear built from scratch with a shared love for everyday style.
                    </p>
                </div>

                <div className="flex flex-col md:flex-row gap-8 justify-between items-start md:items-end border-b border-neutral-100 pb-8">
                    <div className="flex flex-wrap gap-3">
                        {categories.map(cat => (
                            <button
                                key={cat}
                                onClick={() => setSelectedCategory(cat)}
                                className={`px-6 py-2.5 rounded-full text-[10px] font-bold uppercase tracking-widest transition-all duration-300 ${selectedCategory === cat
                                    ? "bg-black text-white"
                                    : "bg-neutral-50 text-neutral-400 hover:bg-neutral-100"
                                    }`}
                            >
                                {cat}
                            </button>
                        ))}
                    </div>

                    <div className="relative w-full md:w-80 group">
                        <Search className="absolute left-0 top-1/2 -translate-y-1/2 w-4 h-4 text-neutral-300 group-focus-within:text-black transition-colors" />
                        <input
                            placeholder="SEARCH BASICS..."
                            className="w-full bg-transparent border-none pl-8 py-3 text-[10px] font-bold tracking-[0.2em] outline-none placeholder:text-neutral-200"
                            value={searchQuery}
                            onChange={(e) => setSearchQuery(e.target.value)}
                        />
                        <div className="absolute bottom-0 left-0 w-full h-px bg-neutral-100 group-focus-within:bg-black transition-all" />
                    </div>
                </div>
            </div>

            <div className="grid grid-cols-2 lg:grid-cols-4 gap-x-4 md:gap-x-8 gap-y-16">
                {filteredProducts.map((product) => (
                    <motion.div
                        layout
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        key={product.id}
                        className="group flex flex-col gap-6"
                    >
                        {/* Image Reveal Container */}
                        <div className="relative aspect-[4/5] overflow-hidden bg-neutral-50">
                            <Link href={`/products/${product.id}`} className="block w-full h-full">
                                <Image
                                    src={product.image_url}
                                    alt={product.name}
                                    fill
                                    className="object-cover transition-transform duration-700 ease-out group-hover:scale-105"
                                />
                            </Link>

                            {/* Overlay Reveal */}
                            <div className="absolute inset-0 bg-black/40 opacity-0 group-hover:opacity-100 transition-all duration-300 flex items-center justify-center backdrop-blur-[2px] pointer-events-none">
                                <button
                                    onClick={(e) => {
                                        e.stopPropagation();
                                        e.preventDefault();
                                        addItem(product);
                                    }}
                                    className="bg-white text-black px-8 py-4 rounded-full text-[10px] font-bold uppercase tracking-widest transform translate-y-4 group-hover:translate-y-0 transition-transform duration-300 hover:bg-black hover:text-white pointer-events-auto shadow-xl"
                                >
                                    Quick Add
                                </button>
                            </div>
                        </div>

                        {/* Product Meta */}
                        <div className="space-y-2">
                            <div className="flex justify-between items-start gap-4">
                                <h3 className="text-sm font-bold uppercase tracking-tight leading-tight group-hover:underline decoration-1 underline-offset-4">
                                    {product.name}
                                </h3>
                                <div className="text-right flex flex-col items-end shrink-0">
                                    <span className="text-sm font-bold">
                                        {formatCurrency(product.sale_price || product.price)}
                                    </span>
                                    {product.sale_price && (
                                        <span className="text-[10px] text-neutral-400 line-through font-medium grayscale opacity-50">
                                            {formatCurrency(product.price)}
                                        </span>
                                    )}
                                </div>
                            </div>
                            <p className="text-[10px] text-neutral-400 uppercase tracking-widest font-bold">
                                {product.category}
                            </p>
                        </div>
                    </motion.div>
                ))}
            </div>

            {filteredProducts.length === 0 && (
                <div className="text-center py-40">
                    <h3 className="text-xl font-bold uppercase tracking-widest mb-4">No essentials found</h3>
                    <p className="text-neutral-400 text-sm">Try a different search or category.</p>
                </div>
            )}
        </div>
    );
}
